<template>
  <div class="tree-table">
    <div class="tree-head">
      <table v-if="!isAna">
        <tr>
          <td class="td1">组织部门</td>
          <td class="td2">部门编码</td>
          <td class="td2">排序</td>
          <td class="td3">状态</td>
          <!-- <td class="td5">发布时间</td> -->
          <td class="td6">操作</td>
        </tr>
      </table>
      <table v-if="!!isAna">
        <tr>
          <td class="td1" style="width:400px">组织部门</td>
          <td class="td4">服务事项总数</td>
          <td class="td4">有服务指南</td>
          <td class="td4">无服务指南</td>
          <td class="td4">有信息化支持</td>
          <td class="td4">无信息化支持</td>
          <td class="td4">信息化覆盖率</td>
        </tr>
      </table>
    </div>
    <div id="scrollWrap" class="tree-wrap">
      <div class="tree-body">
        <table v-if="treeDataSource.length > 0">
          <tbody>
            <tr>
              <td>
                <tree-item
                  :isAna="isAna"
                  v-for="(model, i) in treeDataSource"
                  :key="'root_node_' + i"
                  :root="0"
                  :num="i"
                  @actionFunc="actionFunc"
                  @deleteFunc="deleteFunc"
                  @startRejectFunc="startRejectFunc"
                  @handlerExpand="handlerExpand"
                  @handleRouter="handleRouter"
                  :nodes="treeDataSource.length"
                  :trees.sync="treeDataSource"
                  :model.sync="model"
                ></tree-item>
              </td>
            </tr>
          </tbody>
        </table>

        <table v-else>
          <tr>
            <td style="text-align: center;border:1px solid #e8e8e8;">暂无数据</td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "treeTable",
  props: ["list", "isAna"],
  data() {
    return {
      isDesc: false,
      treeDataSource: []
    };
  },

  watch: {
    list: {
      handler() {
        this.initTreeData();
      },
      deep: true
    },
    isDesc(nelVal, oldVal) {
      this.$emit("orderByFunc", nelVal);
    }
  },
  computed: {},
  methods: {
    handleRouter(model, type) {
      this.$emit("handleRouter", model, type);
    },
    initTreeData() {
      // 这里一定要转化，要不然他们的值监听不到变化
      let tempData = JSON.parse(JSON.stringify(this.list));
      let reduceDataFunc = (data, level) => {
        data.map((m, i) => {
          m.isExpand = !m.isExpand ? false : m.isExpand;
          m.children = m.children || [];
          m.level = level;
          m.bLeft = level === 1 ? 34 : (level - 2) * 16 + 34;
          m.Experience = m.Experience || "无";
          if (m.children.length > 0) {
            reduceDataFunc(m.children, level + 1);
          }
        });
      };
      reduceDataFunc(tempData, 1);
      this.treeDataSource = tempData;
    },
    getMore() {
      alert("滚动到底部加载更多");
      // 滚动到最后
      $("#scrollWrap").mCustomScrollbar("scrollTo", "top", {
        scrollInertia: 0
      });
    },
    // 编辑,新增
    actionFunc(m, type, parentNode) {
      this.$emit("actionFunc", m, type, parentNode);
    },
    // 删除
    deleteFunc(m) {
      this.$emit("deleteFunc", m);
    },
    startRejectFunc(m, type) {
      this.$emit("startRejectFunc", m, type);
    },
    // 展开
    handlerExpand(m) {
      this.$emit("handlerExpand", m);
    }
  },
  components: {
    treeItem: () => import("./tree-item.vue")
  },
  mounted() {
    const vm = this;
    vm.$nextTick(() => {
      vm.initTreeData();
    });
  }
};
</script>

<style lang="scss">
.tree-table {
  width: 100%;
  position: relative;
  background: #fff;
  .center {
    text-align: center;
  }
  .tree-head table {
    td {
      font-weight: 700;
      border: 1px solid #e8e8e8;
      border-bottom: none;
      text-align: center;
    }
  }
  table {
    width: 100%;
    text-align: center;
    border-collapse: collapse;
    border-spacing: 0;
    td {
      word-break: break-all;
      word-wrap: break-word;
      font-size: 16px;
    }
  }
  .td-border {
    border-bottom: 1px solid #e8e8e8;
    .leve {
      // 屏蔽双击不能选择文本样式
      -webkit-user-select: none;
      -moz-user-select: none;
      -o-user-select: none;
      user-select: none;
      &:hover {
        background-color: #f7f9ff;
      }
    }
  }
  th,
  td {
    font-weight: 400;
    text-align: left;
  }
  .td1 {
    width: 30%;
    padding-left: 30px;
    border: 1px solid #e8e8e8;
    border-top: none;
  }
  .td2 {
    width: 15%;
    text-align: center;
    border: 1px solid #e8e8e8;
    border-top: none;
  }
  .td3 {
    width: 15%;
    text-align: center;
    border-bottom: 1px solid #e8e8e8;
  }
  .tdbm {
    width: 400px;
    padding-left: 30px;
    border: 1px solid #e8e8e8;
    border-top: none;
  }
  .td4 {
    width: calc((100vw - 500px) / 6);
    border: 1px solid #e8e8e8;
    text-align: center;
  }
  .td5 {
    width: 220px;
  }
  .td6 {
    width: 25%;
    text-align: center;
    border: 1px solid #e8e8e8;
    border-top: none;
  }
  .p20 {
    padding-left: 20px;
  }
  .leve-1 .td1 {
    padding-left: 30px;
  }
  .leve-2 .td1 {
    padding-left: 46px;
  }
  .leve-3 .td1 {
    padding-left: 62px;
  }
  .leve-4 .td1 {
    padding-left: 78px;
  }
  .leve-5 .td1 {
    padding-left: 90px;
  }
  .leve-6 .td1 {
    padding-left: 106px;
  }
  .leve-7 .td1 {
    padding-left: 122px;
  }
  .leve-8 .td1 {
    padding-left: 138px;
  }
  .leve-9 .td1 {
    padding-left: 154px;
  }
  .td4 {
    .unvisibie {
      visibility: hidden;
    }
    .ellipsis {
      display: block;
      max-width: 200px;
    }
    .iconfont {
      color: #414959;
      position: relative;
      top: 0px;
      left: 5px;
      font-size: 14px;
    }
  }
  > div {
    width: 100%;
  }
  .line-height {
    height: 40px;
    line-height: 40px;
  }

  .tree-head {
    height: 40px;
    line-height: 40px;
    background: #fff;
    border-bottom: 2px solid #e8e8e8;
    td {
      color: rgba(0, 0, 0, 0.45);
      border-bottom: 2px solid #e8e8e8;
      color: #333;
    }
    .arrow {
      position: relative;
      display: inline-block;
      width: 16px;
      height: 16px;
      background: #fff;
      span {
        position: absolute;
        display: block;
        width: 0;
        height: 0;
        left: 0;
        border-style: solid;
        border-width: 4px;
      }
      .up-arrow {
        top: 3px;
        border-color: transparent transparent #ddd transparent;
        &.sort {
          border-bottom-color: #3c4454;
        }
      }
      .down-arrow {
        top: 12px;
        border-color: #ddd transparent transparent transparent;
        &.sort {
          border-top-color: #3c4454;
        }
      }
    }
  }
  .ellipsis {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .tree-wrap {
    top: 40px;
    background: #fff;
  }
  .tree-body {
    table {
      table-layout: fixed;
    }
    td {
      height: 40px;
      line-height: 40px;
      color: rgba(0, 0, 0, 0.85);
      font-size: 14px;
      .reset {
        color: #1890ff;
      }
      .delete {
        color: #f5222d;
      }
      .line {
        display: inline-block;
        width: 1px;
        background: rgba(0, 0, 0, 0.09);
        margin: 0 11px;
        height: 14px;
      }
    }
    .td-title {
      position: relative;
      a {
        display: block;
      }
      .tree-close,
      .tree-open {
        position: absolute;
        width: 0;
        height: 0;
        border-color: #999;
        border-style: solid;
        cursor: pointer;
        border-width: 5px;
        z-index: 2;
      }
      .tree-close {
        left: -12px;
        top: 14px;
        border-color: transparent transparent transparent #aaaaaa;
      }
      .tree-open {
        left: -17px;
        top: 17px;
        border-color: #aaaaaa transparent transparent;
      }
    }
    .leve-1 .td-title a {
      width: 230px;
    }
    .leve-2 .td-title a {
      width: 200px;
    }
    .leve-3 .td-title a {
      width: 170px;
    }
    .leve-4 .td-title a {
      width: 140px;
    }
    .leve-5 .td-title a {
      width: 110px;
    }
    .leve-6 .td-title a {
      width: 80px;
    }
    .leve-7 .td-title a {
      width: 50px;
    }
    .leve-8 .td-title a {
      width: 20px;
    }
    .other-node {
      position: relative;
      > .not-border:not(:last-child) {
        position: relative;
        .before-line {
          content: "";
          position: absolute;
          width: 1px;
          height: 100%;
          background-color: #e8e8e8;
          border: none;
          top: -20px;
          z-index: 1;
          left: 34px;
        }
      }
    }
    .not-border {
      .td-title {
        &::before {
          content: "";
          position: absolute;
          width: 1px;
          height: 40px;
          background-color: #e8e8e8;
          border: none;
          left: -12px;
          top: -20px;
          z-index: 1;
        }
        &::after {
          content: "";
          left: -11px;
          position: absolute;
          right: auto;
          background-color: #e8e8e8;
          height: 1px;
          top: 19px;
          width: 16px;
          z-index: 1;
        }
      }
    }
  }
  .t-icon {
    display: inline-block;
    width: 14px;
    height: 18px;
    background-size: 100% 100% !important;
    vertical-align: middle;
    margin: -3px 4px 0 0;
    &.m-dep {
      background: url(data:image/png;base64,)
        no-repeat;
    }
  }
}
</style>



